@import url("https://fonts.googleapis.com/css2?family=Lato:wght@300;400&family=Manrope:wght@300;400;600;700&family=Ubuntu:wght@700&display=swap");
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

html {
  overflow-x: hidden;
}
html.no-scroll {
  overflow: hidden !important;
  height: 100% !important;
}
html.no-scroll body {
  overflow: hidden !important;
  height: 100% !important;
}
html.no-scroll-long {
  overflow-y: scroll !important;
}

::-webkit-scrollbar {
  width: 0.8rem;
}

::-webkit-scrollbar-thumb {
  background-color: rgba(157, 157, 157, 0.7019607843);
  border-radius: 1rem;
}

body {
  font-family: "Manrope", sans-serif;
  font-size: 1.6rem;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  background-color: #DEDEDE;
  transition: all 0.5s ease-in;
  min-height: 100vh;
}

img,
picture {
  width: 100%;
  height: auto;
  display: block;
}

h1 {
  font-size: 6.4rem;
  font-weight: bold;
}

h2 {
  font-size: 2.4rem;
  font-weight: normal;
}

p {
  font-size: 1.8rem;
  line-height: 1.5;
}

h1, h2, p {
  color: #202020;
}

.contenedor {
  max-width: 120rem;
  margin: 0 auto;
}

.contenedor-padding {
  padding: 0 2.5rem;
}
@media (min-width: 480px) {
  .contenedor-padding {
    padding: 0 5rem;
  }
}
@media (min-width: 768px) {
  .contenedor-padding {
    padding: 0 10rem;
  }
}
@media (min-width: 1080px) {
  .contenedor-padding {
    padding: 0 20rem;
  }
}

a {
  text-decoration: none;
  font-size: 1.6rem;
}
@media (min-width: 480px) {
  a {
    font-size: 1.8rem;
  }
}

#contenido {
  transition: all 0.5s ease-out;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.w-100 {
  width: 100%;
}

.margen-cero {
  margin: 0;
}

.blur {
  backdrop-filter: blur(5px);
}

.no-background {
  background-color: rgba(255, 255, 255, 0) !important;
}

.ocultar {
  display: none !important;
}

.mover-Y {
  transform: translateY(100vh);
}

.modo-oscuro {
  background-color: #202020;
}
.modo-oscuro .barra {
  background-color: rgba(50, 50, 50, 0.7019607843);
}
.modo-oscuro .barra svg g {
  fill: #DEDEDE;
}
.modo-oscuro .bubbles svg g path {
  fill: rgba(207, 138, 189, 0.5098039216);
  stroke: rgba(207, 138, 189, 0.5098039216);
}
.modo-oscuro .nav-desktop a {
  color: #DEDEDE;
}
.modo-oscuro .nav-desktop a::after,
.modo-oscuro .seccion-proyectos .contenedor-proyecto::after,
.modo-oscuro .nav-telefono .linea {
  background-color: #DEDEDE;
}
.modo-oscuro h1,
.modo-oscuro h2,
.modo-oscuro .contenido-principal .nombre,
.modo-oscuro .dev {
  color: #DEDEDE;
}
.modo-oscuro p,
.modo-oscuro span,
.modo-oscuro .dev span,
.modo-oscuro .trabajos-destacados,
.modo-oscuro .trabajos-destacados svg,
.modo-oscuro .ver-proyectos a {
  color: rgba(222, 222, 222, 0.8509803922);
  stroke: rgba(222, 222, 222, 0.8509803922);
}
.modo-oscuro .boton-contacto {
  color: #202020;
}
.modo-oscuro .boton-contacto svg {
  stroke: #202020;
}
.modo-oscuro .contenido-proyecto {
  background-color: #323232;
}
.modo-oscuro .contenedor-proyecto .imagen-proyecto a {
  background-color: #1a1919;
}
.modo-oscuro .contenido-proyecto .tecnologias-proyecto {
  color: #cf8abd;
}
.modo-oscuro .ver-proyectos a:hover {
  color: #202020;
}
.modo-oscuro .contenedor-proyecto .imagen-proyecto div {
  filter: grayscale(70%) contrast(1) brightness(15%) opacity(95%);
}
@media (min-width: 768px) {
  .modo-oscuro .contenedor-proyecto .imagen-proyecto div {
    filter: grayscale(100%) contrast(1) brightness(60%) opacity(75%);
  }
}
.modo-oscuro .contenedor-proyecto .imagen-proyecto div:hover {
  filter: grayscale(0%) contrast(1) brightness(70%) opacity(100%);
}
.modo-oscuro .footer p {
  color: #FFFFFF;
}
.modo-oscuro .footer p span {
  color: rgba(222, 222, 222, 0.8509803922);
}
.modo-oscuro .iconos-contacto li:nth-child(1) svg, .modo-oscuro .iconos-contacto li:nth-child(2) svg, .modo-oscuro .iconos-contacto li:nth-child(3) svg, .modo-oscuro .iconos-contacto li:nth-child(4) svg, .modo-oscuro .iconos-contacto li:nth-child(5) svg {
  fill: #FFFFFF;
}
.modo-oscuro .iconos-contacto::after,
.modo-oscuro .iconos-contacto.activo::before {
  background-color: #FFFFFF;
}
.modo-oscuro .iconos-proyecto svg {
  stroke: #FFFFFF;
}

