.portafolio .titulo-portafolio {
  font-size: 3.6rem;
  text-align: center;
  margin: 12rem 0 5rem;
}
.portafolio .seccion-proyectos {
  padding: 0;
}
@media (min-width: 768px) {
  .portafolio .seccion-proyectos {
    gap: 5rem;
  }
}

.barra {
  background-color: rgba(157, 157, 157, 0.7019607843);
  padding: 2rem 0;
  width: 100%;
  position: fixed;
  top: 0;
  z-index: 2;
  transition: all 0.5s ease-in-out;
}
.barra .logo, .barra svg {
  width: 9rem;
  max-height: 3rem;
  z-index: 2;
}
.barra .logo g, .barra svg g {
  fill: #202020;
}
.barra.ocultar {
  top: -7rem;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.nav-telefono {
  position: relative;
  height: 3rem;
  width: 3rem;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
@media (min-width: 768px) {
  .nav-telefono {
    display: none;
  }
}
.nav-telefono .linea {
  height: 0.3rem;
  width: 100%;
  background-color: #202020;
  border-radius: 0.5rem;
  display: block;
  margin-block: 0.2rem;
  transition: 0.3s;
}
.nav-telefono .linea:nth-child(1), .nav-telefono .linea:nth-child(3) {
  width: 70%;
}
.nav-telefono.activo .linea:nth-child(1), .nav-telefono.activo .linea:nth-child(3) {
  width: 100%;
}
.nav-telefono.activo .linea:nth-child(1) {
  transform: rotate(45deg) translateX(0.5rem) translateY(0.45rem);
}
.nav-telefono.activo .linea:nth-child(2) {
  opacity: 0;
}
.nav-telefono.activo .linea:nth-child(3) {
  transform: rotate(-45deg) translateX(0.5rem) translateY(-0.45rem);
}

.nav-desktop {
  position: absolute;
  display: flex;
  flex-direction: column;
  background-color: rgba(255, 255, 255, 0);
  top: 0;
  left: 0;
  height: 0vh;
  width: 100%;
  transition: all 700ms ease;
  overflow: hidden;
}
.nav-desktop a {
  opacity: 0;
  transition: all 700ms ease;
  padding: 2.5rem 0;
  color: #202020;
  font-size: 2.4rem;
  font-weight: 700;
  text-align: center;
  transform: translateY(-100vh);
}
.nav-desktop a:first-of-type {
  margin-top: 10rem;
}
@media (min-width: 768px) {
  .nav-desktop {
    position: inherit;
    height: auto;
    background-color: transparent;
    flex-direction: row;
    width: auto;
    transition: none;
  }
  .nav-desktop a {
    opacity: 1;
    margin: 0 1.5rem;
    transition: none;
    padding: 0;
    text-align: center;
    transform: translateY(0);
    font-size: 1.6rem;
  }
  .nav-desktop a::after {
    content: "";
    display: block;
    height: 0.7rem;
    width: 0%;
    background-color: #202020;
    position: absolute;
    bottom: -0.5rem;
    left: 0;
    transition: 500ms;
  }
}
@media (min-width: 768px) and (min-width: 1080px) {
  .nav-desktop a {
    font-size: 1.8rem;
  }
}
@media (min-width: 768px) {
  .nav-desktop a:first-of-type {
    margin-top: 0;
  }
}
@media (min-width: 768px) {
  .nav-desktop a:hover::after {
    width: 100%;
  }
}
.nav-desktop.activo {
  height: 100vh;
}
.nav-desktop.activo a {
  transform: translateY(0);
  opacity: 1;
}
.nav-desktop.activo a:hover {
  opacity: 0.7;
}
@media (max-height: 480px) {
  .nav-desktop.activo a {
    padding: 1.5rem 0;
  }
  .nav-desktop.activo a:first-of-type {
    margin-top: 5rem;
  }
}

.contenido-principal {
  position: relative;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 9rem 0;
}
@media (min-width: 768px) {
  .contenido-principal {
    padding-bottom: 0;
  }
}
@media (min-width: 1080px) {
  .contenido-principal {
    padding: 17vh 0 0;
  }
}
.contenido-principal h1 {
  font-size: 2.2rem;
  font-weight: 400;
}
@media (min-width: 480px) {
  .contenido-principal h1 {
    font-size: 2.4rem;
  }
}
@media (min-width: 1080px) {
  .contenido-principal h1 {
    font-size: 2.6rem;
  }
}
.contenido-principal .nombre {
  font-size: 3.6rem;
  font-family: "Ubuntu", sans-serif;
  font-weight: 700;
  color: #202020;
  display: block;
  margin-top: 1rem;
}
@media (min-width: 480px) {
  .contenido-principal .nombre {
    font-size: 5.5rem;
  }
}
@media (min-width: 1080px) {
  .contenido-principal .nombre {
    font-size: 6.4rem;
  }
}
.contenido-principal p {
  font-size: 1.8rem;
  max-width: 80rem;
  margin-top: 2rem;
}
.contenido-principal p,
.contenido-principal span {
  font-weight: 600;
}
@media (min-width: 480px) {
  .contenido-principal p,
.contenido-principal span {
    font-size: 2rem;
  }
}
@media (min-width: 1080px) {
  .contenido-principal p,
.contenido-principal span {
    font-size: 2.2rem;
  }
}
.contenido-principal span {
  color: #1a1919;
}

.boton-contacto {
  background-color: #9D9D9D;
  border-radius: 0.4rem;
  color: #FFFFFF;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1rem;
  height: 5rem;
  width: 20rem;
  margin-top: 4rem;
}
.boton-contacto svg {
  stroke: #FFFFFF;
}

.trabajos-destacados {
  display: flex;
  align-items: center;
  justify-content: center;
  color: #1a1919;
  text-transform: uppercase;
  letter-spacing: 0.1rem;
  font-size: 1.4rem;
  gap: 1rem;
  margin-top: 8vh;
  height: 5rem;
  animation: bounce 0.5s alternate infinite ease-in;
}
@keyframes bounce {
  0% {
    transform: translateY(-0.3rem);
  }
  100% {
    transform: translateY(0);
  }
}
@media (min-width: 768px) {
  .trabajos-destacados {
    font-size: 1.8rem;
    margin-top: 15vh;
  }
}

.iconos-contacto {
  opacity: 0;
  height: 0;
  transform: translateY(-8rem);
  overflow: hidden;
}
.iconos-contacto li {
  list-style: none;
}
.iconos-contacto li:first-of-type svg {
  fill: #000000;
}
.iconos-contacto li a {
  display: inline-block;
  padding: 1rem;
  transition: all 0.25s ease-out;
}
.iconos-contacto li a:hover {
  transform: translateY(-0.5rem);
}
@media (min-width: 768px) {
  .iconos-contacto {
    width: 4rem;
    position: fixed;
    bottom: 3rem;
    color: #000000;
    left: 3rem;
    opacity: 1;
    transform: translateY(0);
    height: auto;
    max-height: 65vh;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .iconos-contacto::after {
    content: "";
    height: 8rem;
    width: 0.2rem;
    border-radius: 0.5rem;
    background-color: #202020;
  }
}
@media (min-width: 768px) and (min-width: 1080px) {
  .iconos-contacto {
    left: 5rem;
  }
}
.iconos-contacto.activo {
  opacity: 1;
  height: auto;
  width: 100%;
  transform: translateY(0);
  transition: all 500ms ease;
  position: fixed;
  bottom: 20vh;
  padding-top: 10vh;
  display: flex;
  justify-content: center;
  z-index: 2;
}
.iconos-contacto.activo ul {
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-evenly;
  width: 100%;
}
.iconos-contacto.activo::before {
  content: "";
  height: 0.3rem;
  width: 90%;
  background-color: #202020;
  position: absolute;
  top: 5rem;
}
@media (max-height: 480px) {
  .iconos-contacto.activo {
    bottom: 8vh;
  }
  .iconos-contacto.activo::before {
    display: none;
  }
}

.icono {
  cursor: pointer;
}
.icono svg {
  width: 2.5rem;
  height: 2.5rem;
}

.seccion-proyectos {
  min-height: 100vh;
  max-width: 90rem;
  padding: 2rem 0;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}
@media (min-width: 768px) {
  .seccion-proyectos {
    gap: 3.5rem;
  }
}

.contenedor-proyecto {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  min-height: 40vh;
  margin-bottom: 3.5rem;
}
.contenedor-proyecto:last-of-type {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .contenedor-proyecto {
    min-height: 35vh;
  }
}
@media (min-width: 1080px) {
  .contenedor-proyecto {
    min-height: 40vh;
  }
}
.contenedor-proyecto .contenido-proyecto,
.contenedor-proyecto .imagen-proyecto {
  grid-area: 1/1;
  grid-column: 1/13;
}
.contenedor-proyecto .imagen-proyecto {
  position: relative;
  height: 100%;
}
.contenedor-proyecto:nth-child(even) .contenido-proyecto {
  align-items: flex-end;
  text-align: end;
}
@media (min-width: 768px) {
  .contenedor-proyecto {
    column-gap: 1rem;
    margin-bottom: 0;
  }
  .contenedor-proyecto:nth-child(odd) .contenido-proyecto {
    grid-column: 1/8;
  }
}
@media (min-width: 768px) and (min-width: 1080px) {
  .contenedor-proyecto:nth-child(odd) .contenido-proyecto {
    grid-column: 1/7;
  }
}
@media (min-width: 768px) {
